import React, { useEffect, useState } from 'react';
import { Button } from 'reactstrap';

export const Food = ({ id, units, name, group, portion, prot, carb, fat, editable }) => {
  const [_id, setId] = useState(id);
  const [_units, setUnits] = useState(units);
  const [_name, setName] = useState(name);
  const [_group, setGroup] = useState(group);
  const [_portion, setPortion] = useState(portion);
  const [_prot, setProt] = useState(prot);
  const [_carb, setCarb] = useState(carb);
  const [_fat, setFat] = useState(fat);
  const [cals, setCals] = useState(4 * (prot + carb) + fat * 9);
  const [isEditable, setIsEditable] = useState(editable);
  const [isDone, setIsDone] = useState(false);

  useEffect(() => {
    setCals(round(_carb + _prot) * 4 + _fat * 9);
    setIsEditable(editable);
  }, [_prot, _carb, _fat, editable]);

  const handleOnclickEdit = e => {
    setIsEditable(true);
  };

  const handleOnclickDone = e => {
    setIsEditable(false);
  };

  return (
    <tr>
      <th scope="row">{_id}</th>
      <td>
        {isEditable ? (
          <input value={_name} onChange={event => setName(event.target.value)} />
        ) : (
          _name
        )}
      </td>
      <td>
        {isEditable ? (
          <input value={_group} onChange={event => setGroup(event.target.value)} />
        ) : (
          _group
        )}
      </td>
      <td>
        {isEditable ? (
          <input value={_portion} onChange={event => setPortion(event.target.value)} />
        ) : (
          _portion + ' ' + _units
        )}
      </td>
      <td>{cals}</td>
      <td>
        {isEditable ? (
          <input value={_prot} onChange={event => setProt(event.target.value)} />
        ) : (
          _prot
        )}
      </td>
      <td>
        {isEditable ? (
          <input value={_carb} onChange={event => setCarb(event.target.value)} />
        ) : (
          _carb
        )}
      </td>
      <td>
        {isEditable ? <input value={_fat} onChange={event => setFat(event.target.value)} /> : _carb}
      </td>
      <td>
        <Button
          color={!isEditable ? 'primary' : 'danger'}
          onClick={!isEditable ? handleOnclickEdit : handleOnclickDone}
        >
          {!isEditable ? 'edit' : 'done'}
        </Button>
      </td>
    </tr>
  );
};
